body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

::-webkit-scrollbar {
  width: 8px;
  background-color: transparent;

}

/* ползунок скроллбара */
::-webkit-scrollbar-thumb {
  background-color: #c2c2c2;
  border-radius: 5px;

}

.app-wrapper {
    margin: 0 auto;
    display: grid;
    width: 1000px;
    overflow: hidden;
    grid-template-areas:
        "header header"
        "nav content";
    grid-template-rows: 60px;
    grid-auto-columns: 2fr 10fr;
}

body {
    background-color: #e7e7e7;
}

h1, h2, h3, h4 {
    margin: 0;
    padding: 0;
}

button {
    background-color: #378ab7;
    color: white;
    border: none;
    border-radius: 10px;
    padding: 10px;
    cursor: pointer;
    transition: all 1ms;
}

button:hover {
    background-color: #2e7398;
}

textarea {
    padding: 0;
    margin: 0;
}

h1 {
    font-size: 24px;
    font-weight: 400;
}

h2 {
    font-size: 16px;
    font-weight: 400;
}

h3 {
    font-weight: 400;
    font-size: 12px;
}

.header_header__CdjMB {
    grid-area: header;
    background-color: white;
    border-radius: 0 0 5px 5px;
}

.header_logo__Cnuy7 {
    padding-left: 5px;
    width: 60px;
}

.header_login__block__2-JpV {
    float: right;
}
.nav_nav__1OO7- {
    grid-area: nav;
    display: flex;
    flex-direction: column;
    margin-top: 15px;
}

.nav_nav__1OO7- a {
    text-decoration: none;
    color: black;
    transition: color 0.1s;
    padding: 3px 0;
    border-radius: 3px;
    padding-left: 5px;
    margin-right: 10px;
}

.nav_nav__1OO7- a:hover {
    text-decoration: none;
    background-color: lightgray;
}

.nav_nav__1OO7- a.nav_activated__1ba_o {
    background-color: lightgray;
}


